.store {
  background-color: var(--color-primary-900);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.store .content {
  flex-direction: column;
  flex: 1;
  align-items: center;
  padding: 20px;
  display: flex;
}

.store .content h1 {
  color: #fff;
  margin-bottom: 20px;
  font-size: 2em;
}

.store .content-filters {
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.store .content-filters .filter-btn {
  background-color: var(--color-primary-200);
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 20px;
  margin: 5px;
  padding: 8px 16px;
  transition: background-color .3s;
}

.store .content-filters .filter-btn.active {
  background-color: var(--color-primary-500);
}

.store .content-items {
  grid-template-columns: 1fr;
  gap: 20px;
  width: 100%;
  max-width: 600px;
  display: grid;
}

@media only screen and (width >= 600px) {
  .store .content-items {
    grid-template-columns: repeat(2, 1fr);
    max-width: 700px;
  }
}

@media only screen and (width >= 1024px) {
  .store .content-items {
    grid-template-columns: repeat(3, 1fr);
    max-width: 900px;
  }
}

@media only screen and (width >= 1280px) {
  .store .content-items {
    max-width: 1100px;
  }
}

@media only screen and (width >= 1440px) {
  .store .content-items {
    max-width: 1300px;
  }
}

.store .content-items-item {
  background-color: var(--color-primary-500);
  cursor: pointer;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 4px #0000001a;
}

.store .content-items-item .item-image {
  margin-top: 16px;
}

.store .content-items-item h3 {
  margin: 0 0 10px;
}

.store .content-items-item p {
  margin: 5px 0;
}

@media only screen and (width >= 600px) {
  .store .content-items-item {
    grid-template-columns: repeat(2, 1fr);
  }
}
